<script>
  import CardInteractiva from '../components/CardInteractiva.vue' 
  //(para utilizar en localhos)
  //const lstProysJson = '/public/../proyectosWD2021.json';
  //para utilizar en produccion
    const lstProysJson = '../Algo.json';


  export default {
    props: {  
      name: {
        type: String,
        default: 'ListaProyectos'
      }
    },
    components: {
      CardInteractiva
    },
    data() {
      return {
        proyectos:[],
        githubPage: 1,
        page: 1,
        loading: false,
        perPage: 5,

        form: {
          email: '',
          name: '',
          comentari: '',
        },
        show: true
      }
    },
    mounted() {
      this.fetchData()
    },
    methods: {
      async fetchData() {
      const res = await fetch (lstProysJson);
      const val= await res.json();
      this.proyectos=val;
      console.log(val);
      },

      onSubmit(event) {
        event.preventDefault()
        alert(JSON.stringify(this.form))
      },
      onReset(event) {
        event.preventDefault()
        // Reset our form values
        this.form.email = ''
        this.form.name = ''
        this.form.food = null
        this.form.checked = []
        // Trick to reset/clear native browser form validation state
        this.show = false
        this.$nextTick(() => {
          this.show = true
        })
      }
    } 
  }
</script>

